<template>
  <div style="background: #262626;" id="app">
    <nav style="padding: 0 0" class="navbar">
      <router-link class="logo" tag="div" to="/">
        <i style="font-size: 30px" class="fas fa-home"></i>
             <img src="/images/logo.png" alt="">
      </router-link>
    


      <div style="height: 100%;position:absolute;right:0" id="navbarNav">
        <ul v-if="username != '' && username != null" class="">
          <!-- <router-link class="nav-item" tag="li" :to="{ name: 'profile', params: { id: username_id }}">  -->
          <li @click="main_profile()" class="nav-item">
           <i  class="far fa-user-circle"></i>
          </li>

          <!-- </router-link> -->
          <router-link class="nav-item" tag="li" to="/inbox">
            <i   class="fas fa-comments"></i>
          </router-link>
          <li @click="logout()" class="nav-item">
            <i   class="fas fa-sign-out-alt"></i>
          </li>
        </ul>
      </div>
    </nav>

    <router-view
      :show_inbox="show_inbox"
      :username_general="username"
      @updateStatus="updateStatus"
    />
  </div>
</template>

<script>
export default {
  name: "app",
  components: {},
  data() {
    return {
      show_inbox: false,
      username: "",
      username_id: "",
    };
  },
  methods: {
    main_profile() {
      this.$router.push({
        path: "/home",
      });
      this.$nextTick(() => {
        this.$router.push({
          name: "profile",
          params: { id: this.username_id },
        });
      });
    },
    updateStatus(status) {
      this.username = status[0];
      this.username_id = status[1];
    },

    logout() {
      localStorage.removeItem("username");
      localStorage.removeItem("username_id");
      this.username_id = "";
      this.username = "";
      if (this.$route.path != "/") {
        this.$router.push("/");
      }
    },
  },
  created() {
    this.username = localStorage.getItem("username");
    this.username_id = localStorage.getItem("username_id");
  },
};
</script>

