<template>
  <div style="" class="profile pb-5">
    <div v-if="loading_upload" class="loading_upload">
      <div class="spinner">
        <div class="spinner-border text-light" role="status">
          <span class="sr-only">Loading...</span>
        </div>
      </div>
    </div>

    <div style="padding-top: 140px" class="container px-4">
      <div class="mx-auto">
        <!-- Profile widget -->
        <div class=" overflow-hidden">
          <img
            src="https://images.unsplash.com/photo-1522075469751-3a6694fb2f61?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=80"
            alt="..."
            width="130"
            class="photo_de_profil mb-2 img-thumbnail"
          />

          <div
          v-if="user_profile.username"
            style="
              lign-items: center;
              position: realtive;
              padding: 38px 10px !important;
            "
            class="text-white dark_div_profile p-4 d-flex justify-content-end text-center"
          >
            <ul v-if="user_profile != []" class="stat text-white list-inline mb-0">
              <li class="list-inline-item">
                <h5 class="font-weight-bold mb-0 d-block">
                  {{ user_post.length }}
                </h5>
                <small class="text-white">
                  <i class="fas fa-image mr-1"></i>Memes</small
                >
              </li>
              <li class="list-inline-item">
                <h5  class="font-weight-bold mb-0 d-block">{{ friends.length }}</h5>
         
                <small class="text-white">
                  <i class="fas fa-user mr-1"></i>Friends</small
                >
              </li>
              <li  @click="Add_friend()" v-if="add_button()" class="list-inline-item">
                <h5 class="font-weight-bold mb-0 d-block">0</h5>
                <small class="text-white">
                  <i class="fas fa-user mr-1"></i>Add Friend</small
                >
              </li>
              <li style="font-size:25px;color: #e57c5b;" @click="DEL_friend()" v-else-if="$route.params.id != username_id" class="list-inline-item">
               
                <small class="">
                  <i class="fas fa-user mr-1"></i>Friend</small
                >
              </li>
            </ul>
            <br />
            <div
              style="
                position: absolute;
                left: 50%;
                transform: translate(-50%, 35px);
              "
            >
              <h4 v-if="user_profile.username" class="text-white">
                {{ user_profile.username }}
              </h4>
            </div>
          </div>

          <div v-if="username_id == $route.params.id" class="py-3">
            <div class="dark_div_profile add_field shadow-sm">
              <button id="new_btn">
                <i
                  v-if="file != ''"
                  @click="reset_file($event)"
                  style="font-size: 22px"
                  class="fas fa-window-close"
                ></i>
                <div v-if="file == ''">
                  <p class="m-0">Uploads New Image</p>
                  <br />
                  <i class="fas fa-upload"></i>
                </div>
                <p style="font-weight: 100" class="m-0" v-else>
                  {{ file.name }}
                </p>
              </button>

              <br />
              <form @submit.prevent="submit">
                <input
                  style="display: none"
                  class="input_file"
                  type="file"
                  ref="file"
                  name="file"
                  id="uploadFile"
                />

                <button
                  v-if="file != ''"
                  class="btn btn-upload btn-outline-dark btn-sm btn-block"
                >
                  UPLOAD
                </button>
              </form>
            </div>
          </div>

      
        </div>
      </div>
    </div>
    <div class="container py-4">
            <div v-if="this.friends.includes(this.username_id ) || $route.params.id == username_id" class="d-flex align-items-center justify-content-between mb-3">
              <h5 style="color:white" class="mb-0">Recent photos</h5>
              <a href="#" class="btn btn-link text-white">Show all</a>
            </div>
            <div  class="private_div" style="color:white"
             v-if="!this.friends.includes(this.username_id ) && $route.params.id != username_id && user_profile.username">
             <img src="/images/lock.png" alt="">
             <p>This Profile Is Private</p>
           </div>
          </div>


    <div @click="hide_img($event)" v-show="img_show" class="modal_img">
      <img class="img-fluid" :src="this.image" alt="" />
    </div>
    
    <div v-if="this.friends.includes(this.username_id ) || $route.params.id == username_id" class="grid_3 container">
      
      <figure class="grid-img" v-for="post in user_post" :key="post._id">
        <div class="content-img-profile" >

         <img
           v-bind:class="{ red_image: post.dim[0].height > 800 }"
          @click="show_img(post.images_url)"
          class="img-profile"
          :src="post.images_url"
        />
        <div  class="like_div">
          <div
            @click="like(post._id, post.likes)"
            v-bind:class="{ liked_div: post.likes.includes(username_id) }"
            class="likes"
          >
            <i class="fas fa-thumbs-up"></i>
            <span>{{ post.likes.length }}</span>
          </div>
          
        </div>
            
           </div>
      </figure>
    </div>

  </div>
</template>

<script>
    
import axios from "axios";
import io from "socket.io-client";
var socket = io("");
export default {
  name: "profile",
  components: {},
  data() {
    return {
      loading_upload: false,
      file: "",
      username: "",
      username_id: "",
      user_post: [],
      user_profile: [],
      show_component: true,
      img_show: false,
      image: "",
      friends :[]
    };
  },
  watch: {},
  methods: {
    DEL_friend(){
      socket.emit("DEL_FREIND",{"profile_id" :  this.$route.params.id,"new_friend" : this.username_id})
    },
    Add_friend(){
      socket.emit("ADD_FREIND",{"profile_id" :  this.$route.params.id,"new_friend" : this.username_id})
    },
    add_button(){
   if (   this.$route.params.id != this.username_id && !this.friends.includes(this.username_id)) {
      return true
   }
     
    },
    like(id) {
      this.get(id) 
      socket.emit("LIKE", {
        post_id: id,
        user_like: this.username_id,
        profile: this.$route.params.id,
      });
    },
    hide_img(e) {
      if (e.target.className == "modal_img") {
        this.img_show = false;
      }
    },
    show_img(img) {
      this.image = img;
      this.img_show = true;
    },
    submit() {
      var today = new Date();
      var date =
        today.getDate() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getFullYear();

      var h = (today.getHours() < 10 ? "0" : "") + today.getHours();
      var m = (today.getMinutes() < 10 ? "0" : "") + today.getMinutes();
      var time = h + ":" + m;
      this.file = this.$refs.file.files[0];
      const formData = new FormData();
      formData.append("file", this.file);
      formData.append("user", this.username_id);
      formData.append("username", this.username);
      formData.append("date", date);
      formData.append("time", time);

      axios.post("/image", formData).then((res) => {
        console.log(res.status);
        if (res.status === 200) {
          this.loading_upload = false;
          this.$router.push({
            name: "home",
          });
        }
      });
      this.loading_upload = true;
      document.getElementById("uploadFile").value = "";
    },
    get(id) {
      for (let i = 0; i < this.user_post.length; i++) {
        if (this.user_post[i]._id == id) {
          if (this.user_post[i].likes.includes(this.username_id)) {
            const index = this.user_post[i].likes.indexOf(this.username_id);
            if (index > -1) {
              this.user_post[i].likes.splice(index, 1);
            }
          } else {
            this.user_post[i].likes.push(this.username_id);
          }
        }
      }
    },
  },
  mounted() {
     var new_btn = document.querySelector("#new_btn");
    var file = document.querySelector(".input_file");
    if (new_btn && file) {
      

    new_btn.onclick = (e) => {
      e.preventDefault();
      console.log(e.target.className);
      if (e.target.className == "fas fa-window-close") {
        this.file = "";
      } else {
        file.click();
      }
      file.onchange = () => {
        this.file = this.$refs.file.files[0];
        console.log(this.file);
      };
    };
        }
  },
  created() {
    // var _this = this


    
    // socket.once("FRIEND",(data)=>{
    //   console.log(data)
    // })


    this.username = localStorage.getItem("username");
    this.username_id = localStorage.getItem("username_id");
    if (this.username == null || this.username == "") {
      this.$router.push({
        name: "home",
        params: { username_empty: true, room: this.$route.params.username },
      });
    }

    socket.emit("GET_POSTS_BY_USER", this.$route.params.id);
    socket.on("GET_POSTS_BY_USER", (posts) => {
      this.user_post = posts;
    });

    socket.emit("GET_FRIEND", this.$route.params.id);
    socket.on("GET_FRIEND", (data) => {
      this.user_profile = data;
      this.friends = data.friends
    });

    socket.on("GET_FRIEND_FALSE", () => {
      this.$router.push({
        name: "home",
      });
    });
  },
};
</script>

<style scoped>
.share_div {
  margin-left: auto;
  width: 69px;
  display: flex;
  display: flex;
  align-items: center;
  justify-content: center;
}
.liked_div {
  color: #e67d5a !important;
}
.likes:hover {
  background: #e5e5e51e;
}

.likes:focus {
  background: none;
}

.like_div {
  z-index: 99999999999999999999;
  position: relative;
  width: 100%;
  height: 50px;
  
}
.likes {
  cursor: pointer;
  position: relative;
  height: 100%;
  color: #818181;
  width: 75px;
  padding: 0 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 3px;
  border-right: 0px;
  border-top: 0px;
}

.likes > span {
  margin-left: 3px;
  font-size: 15px;
  font-weight: bold;
  text-align: left;
  padding-left: 3px;
}
.fa-thumbs-up {
  font-size: 22px;
  transform: translate(1px, -2px);
}
.image {
  transition: 0.3s;
  position: absolute;
  width: 100%;
  height: 100;
  height: 100%;
}
/* .grid-img > img  {
 transition: 0.2s;
} */
.grid-img {
  transition: 0.3s;
  filter: brightness(0.8);
}
.grid-img:hover {
  filter: brightness(1);
}
/* .img-profile:hover{
 filter: brightness(0.8);
} */



.profile-head {
  transform: translateY(5rem);
}

.cover {
  background-image: url(https://images.unsplash.com/photo-1530305408560-82d13781b33a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1352&q=80);
  background-size: cover;
  background-repeat: no-repeat;
}

body {
  background: #654ea3;
  background: linear-gradient(to right, #e96443, #904e95);
  min-height: 100vh;
}
body {
  font-family: "Open Sans", Arial, sans-serif;
}

* {
  box-sizing: border-box;
}

.container {
  width: 100%;
  max-width: 930px;
  margin-right: auto;
  margin-left: auto;
}

.column {
  width: 33.33%;
  margin-right: 30px;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 28px;
}

.column > img {
  width: 100%;
  height: 100%;
}

@media (max-width: 760px) {
  .row {
    margin-bottom: 3px;
  }

  .column {
    margin-right: 3px;
  }
}

@media (max-width: 450px) {
  .row {
    flex-direction: column;
    justify-content: center;
  }

  .column {
    width: 100%;
    margin-bottom: 6px;
  }
}

img {
  max-width: 100%;
  display: block;
}

figure {
  position: relative;
  margin: 0;
  display: grid;
  grid-template-rows: 1fr auto;
  margin-bottom: 10px;
  break-inside: avoid;
}

figure > img {
  grid-row: 1 / -1;
  grid-column: 1;
}
</style>